.App{
    font-family: sans-serif;
    background-color: rgb(81,201,171); 
    color: white;
    height: 100vh !important;
    overflow: hidden;
    overflow-y: scroll;
}

.contentt{
    display: flex;
    flex-direction: column;
}

.name-slide{
    height: calc(100vh - 22rem);
    position: relative;
}

.name-holder{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%);
    z-index: 10;
    text-align: center;
}

.name{
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
    font-family: "Gloria Hallelujah";
    font-weight: 600;
    font-size: 10vh;
    margin-bottom: 0 !important;
    padding-bottom: 0 !important;
    line-height: 170%;
    animation: text-grow-shrink 0.4s infinite alternate;
}

@keyframes text-grow-shrink {
    0% {
      letter-spacing: 0;
    }
    100% {
      letter-spacing: 4px;
    }
  }

.title{
    font-weight: 600;
    text-shadow: -1px -1px 0 brown, 1px -1px 0 brown, -1px 1px 0 #ffffff, 1px 1px 0 #ffffff;
    color: brown;
    font-family: "Gloria Hallelujah";
    text-align: center;
    font-size: 4vh;
    line-height: 50%;
}

.bart-head{
    width: 28rem;
    position: absolute;
    left: 50vw;
    bottom: 0;
    transform: translate(-50%);
    z-index: 0;
}

@media screen and (min-width: 1200px) {
    .bart-head{
        width: 28rem;
        position: absolute;
        left: 50vw;
        bottom: 0;  
        transform: translate(-50%);
    }    
}

.name-slide-content{
    position: absolute;
    height: 21rem;
    bottom: 0;
    left: 40%;
}

.bomb{
    margin-top: 40px;
    width: 20rem;
}

.projects{
    margin-top: 12rem;
}


.project{
    width: min-content;
    position: relative;
    margin: auto;
    margin-bottom: 8rem;
    cursor: pointer;
    z-index: 0;
    width: 25rem;
    text-align: center;
}

.project-button{
    margin-top: 20%; 
}

.project-button:hover{
    animation: project-hover-animation 1s;
}

@keyframes project-hover-animation {
    0% { transform:  rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.project-image{
    width: 14rem;
    margin: auto;
    cursor: pointer;
}

.project-title{
    font-family: "Kreon", sans-serif;
    position: relative;
    padding: 5px;
    text-align: center;
    bottom: 3rem;
    font-size: 2rem;
    animation: project-title-animation 0.5s infinite alternate;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

@keyframes project-title-animation {
    0% { transform: translateX(0) }
    25% { transform: translateX(5px) }
    50% { transform: translateX(-5px) }
    75% { transform: translateX(5px) }
    100% { transform: translateX(0) }
}

.contact{
    margin: auto;
    margin-bottom: 25rem;
    width: min-content;
    font-family: "Kreon", serif;
    text-align: center;
    display: flex;
    flex-direction: column;
}

.contact-me{
    animation: contact-me-animation 0.4s infinite alternate;
}

@keyframes contact-me-animation {
    0% { transform: rotate(0deg); }
    25% { transform: rotate(2deg); }
    50% { transform: rotate(0eg); }
    75% { transform: rotate(-2deg); }
    100% { transform: rotate(0deg); }

}

.contact p{
    font-size: 10vh;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
    line-height: 100%;
}

.contact a{
    text-decoration: none;
    color: white;
    font-size: 1.4rem;
    margin-top: 7px;
    letter-spacing: 1px;
}

.contact a:hover{
    letter-spacing: 2px;
}

.bart{
    position: fixed;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 1%);
    background-color: rgb(81,201,171);
}

.bart-body{
    width: 28rem;
    height: 22rem;
}

.eye-left{
    position: absolute;
    top: 14%;
    left: 8%;
    width: 5rem;
    border-radius: 30px;
}

.eye-right{
    position: absolute;
    top: 15%;
    right: 38%;
    border-radius: 20px;
    width: 6rem;
}